        <svg class="w-7 h-7" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M4 6h16M4 12h16M4 18h16"></path></svg>
      </div>
      <ul class="nav-sticky justify-center md:justify-start py-4 uppercase font-medium md:col-start-2 md:col-span-5">
        <li class="px-4 hover:text-primary-500 cursor-pointer" @click="onClickMenu('about')"><a href="#about">Giới thiệu</a></li>
        <li class="px-4 hover:text-primary-500 cursor-pointer" @click="onClickMenu('services')"><a href="#services">Dịch vụ</a></li>
        <li class="px-4 hover:text-primary-500 cursor-pointer" @click="onClickMenu('products')"><a href="#products">Sản phẩm</a></li>
        <li class="px-4 hover:text-primary-500 cursor-pointer" @click="onClickMenu('showcase')"><a href="#showcase">Dự án</a></li>
      </ul>
    </div>

  </div>
</template>

<script>

export default {
  name: 'Navbar',
  data() {
    return {
      isSticky: false
    }
  },
  mounted() {
    let position = document.getElementById('menu-bar').offsetTop
    let sections = document.querySelectorAll('.section')
    let navLinks = document.querySelectorAll('.nav-sticky li a')

    // making navbar sticky
    window.document.onscroll = () => {
      let navBar = document.getElementById('menu-bar')

      if (window.scrollY > position) {
        navBar.classList.remove('menu-bar')
        navBar.classList.add('menu-bar-sticky')
      } else {
        navBar.classList.remove('menu-bar-sticky')
        navBar.classList.add('menu-bar')
      }
    }

    // navbar current section
    window.addEventListener('scroll', e => {
      let fromTop = window.scrollY

      navLinks.forEach(link => {
        let section = document.querySelector(link.hash) //return div based on anchor's href
        
        if (
          section.offsetTop - 150 <= fromTop && 
          section.offsetTop + section.offsetHeight - 150 > fromTop
        ) {
          link.classList.add('li-current')
        } else {
          link.classList.remove('li-current')
        }
      })
    })

  },
  methods: {
    onClickMenu (e) {
      let position = document.getElementById(e)
      window.scrollTo({
        top: position.offsetTop - 60,
        left: 0,
        behavior: 'smooth'
      })
    }
  }
}
</script>

